import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/ferpvue/',
  routes: [
    {
      path: '/',
      name: 'login',
      component: resolve => require(['@/components/login'], resolve)
    },
    {
      path: '/login',
      name: '/login',
      component: resolve => require(['@/components/login'], resolve)
    },
    {
      path: '/index',
      name: 'index',
      component: resolve => require(['@/components/index'], resolve),
      meta: { requiresAuth: true
      }
    },
    {
      path: '/logout',
      name: 'logout',
      component: resolve => require(['@/components/login'], resolve),
      meta: { requiresAuth: true
      }
    }
  ]
})



